export function moveItemToNewList(currentList: any[], targetList: any[], oldIndex: number, newIndex: number) {
  if (currentList.length > 0) {
    const item = currentList.splice(oldIndex, 1)[0];
    targetList.splice(newIndex, 0, item);
  }
}

export function moveItemInList(list: any[], oldIndex: number, newIndex: number) {
  const item = list[oldIndex];
  list.splice(oldIndex, 1);
  list.splice(newIndex, 0, item);
}
