import { Injectable, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { MdbSortableListDirective } from './mdb-sortable-list.directive';

@Injectable({
  providedIn: 'root'
})
export class MdbSortableService implements OnDestroy {

  constructor() { }

  sortableContainer: MdbSortableListDirective;

  dragStart = new Subject<any>();
  dragMove = new Subject<any>();
  dragEnd = new Subject<any>();

  ngOnDestroy() {
    this.dragStart.complete();
    this.dragMove.complete();
    this.dragEnd.complete();
  }
}
